/* eslint-disable no-prototype-builtins */
import { GET_DEPT_ROOT, GET_DEPT_TREE, GET_USER_BY_DEPT, GET_PAGE_EMPLOYEE } from '@/api'

const toHump = name => name.replace( /\_(\w)/g, function ( all, letter ) {
  return letter.toUpperCase()
} )
// nodeID  
async function getDepChildNode ( orgId ) {
  const promises = [GET_DEPT_TREE( { orgId } )]
  let res = []
  // loadUser && promises.push( GET_USER_BY_DEPT( { deptId: orgId } ) )
  try {
    res = await Promise.all( promises )
  } catch ( error ) {/* this.$message.error('')*/ }
  const nodes = res.reduce( ( p, c ) => {
    return [...p, ...c.data]
  }, [] )
  return nodes
}
// promise
async function loadDepOrUser ( node, loadDep = true ) {
  let nodeData = []
  if ( node.level === 0 ) { // 
    const test = await getRootDept()  // 
    nodeData = [test]
  } else if ( node.level === 1 ) {
    nodeData = await getDepChildNode( node.data.deptId )  // 
  } else if ( !loadDep && node.level === 2 ) {
    nodeData = ( await GET_PAGE_EMPLOYEE() ).data  // 
  }
  return nodeData
}
// 
// nodeID  
async function getRootDept () {
  let res = []
  try {
    res = ( await GET_DEPT_ROOT() ).data
  } catch ( err ) { }
  return res
}

function loadDepData ( node ) {
  return loadDepOrUser( node ) // promise
}

function loadUserData ( node ) {
  return loadDepOrUser( node, false )  // promise
}

const defaultOption = {
  tabName: '',  // 
  tabKey: 'dep', // selected eg: {dep: [], role: []}
  children: 'children', // 
  // id 
  nodeId: function ( data ) {
    return data.hasOwnProperty( 'userId' ) ? data.userId : data.deptId
  },
  //   string | function
  label: function ( data, node ) {
    return data.hasOwnProperty( 'userId' ) ? data.userName : data.deptName
  },
  //   string | function
  isLeaf: function ( data, node ) {
    return data.hasOwnProperty( 'userId' ) // empID  
  },
  //     
  // function
  searchResTip: function ( data ) {
    return '' + data.deptId
  },
  //  
  disabled: function ( data, node ) {
    return false
  },
  //  promise
  onload: loadDepData,
  //  
  onsearch: async function ( searchString, resolve, reject ) {
    // const param = { page: 1, limit: 200, searchName: searchString }
    resolve( ( await GET_PAGE_EMPLOYEE() ).data )
  }
}

export const DEP_CONFIG = Object.assign( {}, defaultOption )
export const ROLE_CONFIG = Object.assign( {}, defaultOption, { tabKey: 'role', tabName: '' } )
export const USER_CONFIG = Object.assign( {}, defaultOption, { tabKey: 'user', tabName: '', onload: loadUserData, disabled: ( data, node ) => !data.hasOwnProperty( 'userId' ) } )
const DEP_USER_CONFIG = Object.assign( {}, defaultOption, { tabKey: 'dep&user', tabName: '', onload: loadUserData, disabled: () => false } )
const POSITION_CONFIG = Object.assign( {}, defaultOption, { tabKey: 'position', tabName: '' } )
export const CONFIG_LIST = [DEP_CONFIG, ROLE_CONFIG, USER_CONFIG, DEP_USER_CONFIG, POSITION_CONFIG]
