<template>
  <div class="setting-container">
    <el-form ref="elForm" :model="formData" :rules="rules" size="medium" label-width="100px"
      label-position="top">
      <el-form-item label="" prop="flowName">
        <el-input v-model="formData.flowName" placeholder="" clearable :style="{width: '100%'}">
        </el-input>
      </el-form-item>
      <el-form-item label="" prop="flowGroup">
        <el-select v-model="formData.flowGroup" placeholder="" clearable :style="{width: '100%'}">
          <el-option v-for="(item, index) in flowGroupOptions" :key="index" :label="item.label"
            :value="item.value" :disabled="item.disabled"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="" prop="approver">
        <el-button  plain size="mini"></el-button>
      </el-form-item>
      <el-form-item label="" prop="icon">
        <img :src="activeIconSrc" style="width: 28px;height: 28px;vertical-align: middle;">
        <el-button  plain size="mini" @click="dialogVisible = true" style="margin-left: 10px;"></el-button>
      </el-form-item>
      <el-form-item label="" prop="flowRemark">
        <el-input v-model="formData.flowRemark" type="textarea" placeholder="" :maxlength="100"
          show-word-limit :autosize="{minRows: 4, maxRows: 4}" :style="{width: '100%'}"></el-input>
      </el-form-item>
    </el-form>
    <el-dialog
      title=""
      :visible.sync="dialogVisible"
      width="612px">
      <img 
      v-for="(icon, index) in iconList" 
      :key="index" :src="icon.src" 
      class="icon-item" 
      :class="{active: selectedIcon === icon.id}"
      @click="selectedIcon = icon.id">
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false; selectedIcon = activeIcon" size="small"> </el-button>
        <el-button type="primary" @click="dialogVisible = false; activeIcon = selectedIcon" size="small"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
export default {
  components: {},
  props: ['tabName'],
  data() {
    const req = require.context( '@/assets/approverIcon', false, /\.png$/ )
    const iconList = req.keys().map((t, idx) => ({src: req(t), id: idx}))
    return {
      dialogVisible: false,
      activeIcon: iconList[0].id,
      selectedIcon: iconList[0].id,
      formData: {
        flowName: '',
        flowImg: '',
        flowGroup: undefined,
        flowRemark: undefined,
      },
      rules: {
        flowName: [{
          required: true,
          message: '',
          trigger: 'blur'
        }],
        flowGroup: [{
          required: true,
          message: '',
          trigger: 'change'
        }],
      },
      iconList,
      flowGroupOptions: [{
        "label": "",
        "value": 1
      }, {
        "label": "",
        "value": 2
      }, {
        "label": "",
        "value": 3
      }, {
        "label": "",
        "value": 4
      }, {
        "label": "",
        "value": 5
      }, {
        "label": "",
        "value": 6
      }, {
        "label": "",
        "value": 7
      }],
    }
  },
  computed: {
    activeIconSrc(){
      const icon = this.iconList.find(t => t.id === this.activeIcon)
      return icon ? icon.src : ''
    }
  },
  mounted() { },
  methods: {
    // 
    getData() {
      return new Promise((resolve, reject) => {
        this.$refs['elForm'].validate(valid => {
          if (!valid) {
            reject({ target: this.tabName})
            return
          }
          resolve({ formData: this.formData, target: this.tabName})  // TODO 
        })
      })
    },
  }
}
</script>
<style lang="stylus" scoped>
.icon-item{
  width 40px
  height 40px 
  margin: 6px;
  position relative
  cursor pointer
  opacity .5

  &.active{
    opacity 1
  }
  &:hover{
    opacity 1
  }
}

.setting-container{
  width: 600px;
  height: 100%;
  margin: auto;
  background: white;
  padding: 16px;

  >>>.el-form--label-top .el-form-item__label{
    padding-bottom: 0;
  }
}
</style>>

